@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;1,100&display=swap);
* {
  box-sizing: border-box;
}

:root {
  --BGC-header: #032541;
  --BGC-backdrop-gradient1: #033654d7;
  --BGC-backdrop-gradient2: #017aa3c7;
  --BGC-button: #13c9bf;
  --posi-header-bar: 0;
}

body {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: white;
}

#root .app {
  width: 100%;
  height: 100%;
  position: relative;
}
#root .app .header {
  background-color: #032541;
  background-color: var(--BGC-header);
  position: fixed;
  width: 100%;
  transition: 0.25s ease-in-out;
  z-index: 2;
}
#root .app .header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#root .app .header-bar div {
  display: flex;
}
#root .app .header-bar-link {
  font-size: 20px;
  color: white;
  margin: 0 10px 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#root .app .header-bar-link .home {
  width: 60px;
  object-fit: cover;
}
#root .app .header-bar-text {
  padding: 5px;
}
#root .app .header-bar-search {
  margin-right: 4%;
  width: 50px;
  height: 50px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 500;
  border: none;
  background-color: transparent;
  color: #19d0b2;
  border: none;
  outline: none;
}
#root .app .header-search {
  background-color: #fff;
}
#root .app .header-search-tool {
  border-bottom: 1px solid grey;
  display: flex;
  justify-content: left;
  align-items: center;
}
#root .app .header-search-tool .search-icon {
  margin-left: 4%;
  font-size: 25px;
}
#root .app .header-search-tool-filled {
  margin: 5px;
  font-size: 20px;
  border: none;
  outline: none;
  padding: 5px;
  width: 90%;
  margin-left: 20px;
}
#root .app .header-search-tool .close-icon {
  margin: 5px;
  margin-right: 5%;
}
#root .app .header-search-tool .close-icon-wait {
  width: 20px;
  height: 20px;
  position: relative;
  display: inline-block;
}
#root .app .header-search-tool .close-icon-wait::after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  border: 1px solid grey;
  border-radius: 50%;
  z-index: 2;
  clip: rect(0, 18px, 20px, 2px);
  box-sizing: border-box;
  -webkit-animation: header-search-icon-wait-outside 0.5s linear 0s 1;
          animation: header-search-icon-wait-outside 0.5s linear 0s 1;
}
@-webkit-keyframes header-search-icon-wait-outside {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes header-search-icon-wait-outside {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
#root .app .header-search-tool .close-icon-wait::before {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  border: 1px solid grey;
  border-radius: 50%;
  clip: rect(2px, 10px, 9px, 0px);
  box-sizing: border-box;
  top: 5px;
  left: 5px;
  -webkit-animation: header-search-icon-wait-inner 0.5s linear 0s var(--run-search-icon-wait);
          animation: header-search-icon-wait-inner 0.5s linear 0s var(--run-search-icon-wait);
}
@-webkit-keyframes header-search-icon-wait-inner {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
@keyframes header-search-icon-wait-inner {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
#root .app .header-search-tool .close-icon-display {
  font-size: 20px;
}
#root .app .header-search-result-trending {
  border-bottom: 1px solid grey;
  padding-top: 2px;
  padding-bottom: 2px;
}
#root .app .header-search-result-trending .trending-icon {
  margin: 0 0 0 4%;
  font-size: 25px;
  font-weight: bolder;
}
#root .app .header-search-result-trending .trending-text {
  font-size: 30px;
  font-weight: bold;
  padding-left: 10px;
}
#root .app .header-search-result-items {
  border-bottom: 1px solid grey;
  padding-top: 2px;
  padding-bottom: 2px;
}
#root .app .header-search-result-items .icon {
  margin: 0 0 0 4%;
  font-size: 20px;
  font-weight: bolder;
}
#root .app .header-search-result-items .text {
  font-size: 20px;
  margin-left: 20px;
}
#root .app .contentApp {
  width: 100%;
  min-height: 200vh;
  z-index: -1;
}
#root .app .contentApp .home {
  width: 100%;
  left: 0;
}
#root .app .contentApp .home-backdrop {
  width: 100%;
  height: 50vh;
  background-image: linear-gradient(to right, #033654d7, #017aa3c7), url(/WebPhimClub/static/media/film.6e3c5348.jpg);
  background-image: linear-gradient(to right, var(--BGC-backdrop-gradient1), var(--BGC-backdrop-gradient2)), url(/WebPhimClub/static/media/film.6e3c5348.jpg);
  background-position: center;
  background-color: #032541;
  background-color: var(--BGC-header);
  background-repeat: no-repeat;
  background-size: cover;
}
#root .app .contentApp .home-backdrop-header {
  position: absolute;
  top: 14vh;
  margin-left: 30px;
  color: white;
  font-size: 30px;
}
#root .app .contentApp .home-backdrop-header h2 {
  font-size: 20px;
  margin-top: 20px;
}
#root .app .contentApp .home-backdrop-search {
  border-radius: 40px;
  position: absolute;
  margin: 5px 4%;
  width: 92%;
  top: 30vh;
  background-color: #fff;
  font-size: 20px;
}
#root .app .contentApp .home-backdrop-search input {
  border-radius: 40px;
  font-size: 20px;
  width: 80%;
  border: none;
  outline: none;
  padding: 15px 30px;
}
#root .app .contentApp .home-backdrop-search button {
  font-size: 20px;
  padding: 15px 30px;
  color: white;
  background-color: #13c9bf;
  background-color: var(--BGC-button);
  border: none;
  border-radius: 40px;
  outline: none;
  float: right;
}
#root .app .contentApp .home-popular {
  width: 100%;
  padding: 20px 20px 0 20px;
}
#root .app .contentApp .home-popular h2 {
  font-size: 25px;
  font-weight: bold;
  padding: 10px 0;
  margin-left: 10px;
  margin-bottom: 10px;
}
#root .app .contentApp .home-popular-header {
  display: flex;
}
#root .app .contentApp .home-popular-header-toggle {
  position: relative;
  height: 100%;
  display: flex;
  margin: 8px 30px;
  font-weight: 700;
  border-radius: 20px;
  border: 1px solid #032541;
  border: 1px solid var(--BGC-header);
}
#root .app .contentApp .home-popular-header-toggle .bythetime {
  padding: 7px 20px;
  border-radius: 20px;
}
#root .app .contentApp .home-popular-header-toggle .cover {
  position: absolute;
  width: 82px;
  height: 100%;
  background-color: #032541;
  background-color: var(--BGC-header);
  top: 0;
  border-radius: 20px;
  z-index: -1;
  transition: 0.5s ease-out;
}
#root .app .contentApp .home-popular-display {
  margin-bottom: 20px;
  margin-left: 10px;
  display: flex;
  overflow: scroll;
  overflow-y: hidden;
}
#root .app .contentApp .home-popular-display .movieBanner:not(:first-child) {
  margin-left: 20px;
}
#root .app .contentApp .home-popular-display .movieBanner {
  margin-bottom: 20px;
}
#root .app .footer {
  background-color: #032541;
  background-color: var(--BGC-header);
  background-image: linear-gradient(to right, #032541, #13c9bf);
  background-image: linear-gradient(to right, var(--BGC-header), var(--BGC-button));
  height: 20vh;
  text-align: center;
}
#root .app .footer .text {
  font-size: 30px;
  padding: 20px;
}
#root .app .footer .icon {
  display: flex;
  justify-content: space-around;
}
#root .app .footer .icon .react-icons {
  vertical-align: middle;
  color: var(--head-color);
  font-size: 40px;
}

.movieBanner {
  background-image: url(/WebPhimClub/static/media/bgcOfBanalMovie1.79ee4b3d.jpg);
  background-size: cover;
  border-radius: 10px 10px 0 0;
  display: flex;
  flex-direction: column;
  position: relative;
}
.movieBanner a:active,
.movieBanner a:visited,
.movieBanner a {
  color: black;
}
.movieBanner img {
  width: 170px;
  border-radius: 10px;
  height: 250px;
  object-fit: cover;
  flex-shrink: 0;
}
.movieBanner-title {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}
.movieBanner-title h4 {
  padding: 0 10px;
  height: 50px;
  display: inline-block;
  flex-shrink: 0;
  font-weight: 800;
  margin-bottom: 30px;
  overflow: hidden;
}
.movieBanner-title h5 {
  padding: 0 10px;
  width: 100%;
  flex: 1 1;
  margin-top: auto;
  position: absolute;
  bottom: 0;
  margin-bottom: 10px;
  font-weight: 100;
}

.wrap-circleProcessBar {
  position: absolute;
  top: 230px;
  left: 20px;
}

.link-decoration {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background-color: white;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

.devide {
  width: 100%;
  height: 3px;
  background-color: #032541;
  background-color: var(--BGC-header);
}

