  --BGC: black;
  --color: white;
  --percent: "80deg";
  --width-outermost: 15%;
  width: var(--width);
  height: var(--height);
  position: relative;
}
.circleProgressBar-outside {
  width: var(--width);
  height: var(--height);
  background-color: var(--BGC);
  color: var(--color);
  font-size: var(--font-size);
  border-radius: 50%;
}
.circleProgressBar-outside-left {
  width: 100%;
  height: 100%;
  position: absolute;
  clip: rect(0, calc(var(--width) / 2), var(--width), 0);
}
.circleProgressBar-outside-left-run {
  margin: 0px;
  width: 100%;
  height: 100%;
  position: absolute;
  clip: rect(0, var(--width), var(--width), calc(var(--width) / 2));
}
.circleProgressBar-outside-left-run::after {
  content: "";
  width: calc( var(--width) - var(--border-width) - var(--width-outermost) );
  height: calc(100% - var(--border-width) - var(--width-outermost));
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: var(--border-width) solid var(--border-color);
  border-radius: 50%;
}
.circleProgressBar-outside-right {
  width: 100%;
  height: 100%;
  position: absolute;
  clip: rect(0, var(--width), var(--width), calc(var(--width) / 2));
}
.circleProgressBar-outside-right-run {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 50%;
  clip: rect(0, calc((var(--width)) / 2), var(--width), 0);
}
.circleProgressBar-outside-right-run::after {
  content: "";
  width: calc( var(--width) - var(--border-width) - var(--width-outermost) );
  height: calc(100% - var(--border-width) - var(--width-outermost));
  position: absolute;
  border: var(--border-width) solid var(--border-color);
  border-radius: 50%;
  top: 50%;
  left: 50%;
